import { AdMob, BannerAdSize, BannerAdPosition } from '@capacitor-community/admob';
import { Capacitor } from '@capacitor/core';

const TEST_BANNER_ID = 'ca-app-pub-3940256099942544/6300978111';
const isNative = Capacitor.isNativePlatform();

export const initMobApp = async (): Promise<void> => {
  if (!isNative) {
    console.log('AdMob solo disponible en plataformas nativas');
    return;
  }

  try {
    await AdMob.initialize({
      requestTrackingAuthorization: true,
      testingDevices: [],
      initializeForTesting: false
    });
    console.log('✓ AdMob inicializado');
  } catch (err) {
    console.error('Error inicializando AdMob:', err);
  }
};

export const showBannerAd = async (): Promise<boolean> => {
  if (!isNative) {
    console.log('AdMob solo disponible en plataformas nativas');
    return false;
  }

  const bannerId = import.meta.env.VITE_MOBAPP_BANNER_ID || TEST_BANNER_ID;

  try {
    await AdMob.showBanner({
      adId: bannerId,
      adSize: BannerAdSize.ADAPTIVE_BANNER,
      position: BannerAdPosition.BOTTOM_CENTER,
      margin: 0,
      isTesting: false
    });
    console.log('✓ Banner mostrado:', bannerId);
    return true;
  } catch (err) {
    console.error('Error mostrando banner:', err);
    return false;
  }
};

export const hideBannerAd = async (): Promise<void> => {
  if (!isNative) return;

  try {
    await AdMob.hideBanner();
    console.log('✓ Banner ocultado');
  } catch (err) {
    console.error('Error ocultando banner:', err);
  }
};

export const removeBannerAd = async (): Promise<void> => {
  if (!isNative) return;

  try {
    await AdMob.removeBanner();
    console.log('✓ Banner eliminado');
  } catch (err) {
    console.error('Error eliminando banner:', err);
  }
};